import { DynamoDB } from "@aws-sdk/client-dynamodb";
import { Rekognition } from "@aws-sdk/client-rekognition";
import { nanoid } from "nanoid";
import { PrismaClient } from '@prisma/client'
const prisma = new PrismaClient()

export const handler = async (data: any) => {
    try {
        const objectInfo = data.Records?.[0]?.s3;

        if (!objectInfo) {
            return {
                error: "No object info",
                data,
            };
        }

        const rekognitionResponse = await new Rekognition().detectLabels({
            Image: {
                S3Object: {
                    Bucket: objectInfo.bucket.name,
                    Name: (objectInfo.object.key || "").replace(/\+/g, " ")
                }
            },
            MinConfidence: 80,
            Settings: {
                GeneralLabels: {
                    LabelInclusionFilters: [
                        "Person",
                        "Knife",
                        "Gun",
                        "Weapon",
                    ],
                },
            }
        })

        const dynamoDB = new DynamoDB({ region: "us-east-1" });

        await dynamoDB.putItem({
            TableName: "livebus-dynamodb",
            Item: {
                id: { S: nanoid() },
                name: { S: "test" },
                records: { S: JSON.stringify(rekognitionResponse) },
                insertedAt: { S: new Date().toISOString() }
            }
        })

        console.info(await prisma.camera.findMany());
    } catch (error) {
        console.info(error);
        const dynamoDB = new DynamoDB({ region: "us-east-1" });

        await dynamoDB.putItem({
            TableName: "livebus-dynamodb",
            Item: {
                id: { S: nanoid() },
                name: { S: "test" },
                records: { S: JSON.stringify(error) },
                insertedAt: { S: new Date().toISOString() }
            }
        })

        return {
            error: JSON.stringify(error),
            errorr: 21234,
        };
    }

    return "Hello, world!";
}